import axios from "axios";
import { createAsyncThunk } from "@reduxjs/toolkit";
//import { logIn, logOut, getProfile } from '../services/auth-api';

axios.defaults.baseURL = 'https://connections-api.herokuapp.com/';

export const setToken = token => {
    axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
} //    axios   .     , Bearer   ,     setToken
//    (common)    .


const dellToken = () => {
    axios.defaults.headers.common['Authorization'] = '';
  //  delete axios.defaults.headers.common['Authorization']
} // delete -   ,      ' (common)

// console.log(axios.defaults); //{..., headers: {common: {}, delete: {}, get: {}, head: {}, post: {}, } }
//common: {Accept: "application/json, text/plain, */*", Authorization: "Bearer "}



//    (   authSlice)

export const signUpThunk = createAsyncThunk(
    'auth/register',
    async (credentials, thunkAPI) => {   //  credentials    RegisterPage
        // thunkAPI,   dispatch, getState (  Redux-), extra, requestId, signal, rejectWithValue 
        try {
            const { data } = await axios.post('/users/signup', credentials);
            if('token' in data) setToken(data.token);
            return data;
        } catch (error) {
            return thunkAPI.rejectWithValue(error.message);
        }
    }   
);

export const loginThunk = createAsyncThunk(
    'auth/login',
    async (credentials, { rejectWithValue }) => { //  credentials  {email, password}  LoginPage -  dispatch
        try{
            const { data } = await axios.post('/users/login', credentials)  //     
                if('token' in data) setToken(data.token) 
                return data; // {token: ..., user: {name: ..., email: ...}}
        } catch (error){
            return rejectWithValue(error.response.data.message);
        }
    }
)

export const logoutThunk = createAsyncThunk(
    'auth/logout',
    async (_, { rejectWithValue }) => {
        try{
            await axios.post('/users/logout');
            dellToken();
        } catch (error){
            return rejectWithValue(error.response.data.message);
        }
    }
)

export const fetchCurrentUserThunk = createAsyncThunk( //  refreshUser  //   
    'auth/current',
    async (_, thunkAPI) => { //  credentials  {email, password}  LoginPage -  dispatch
        const state = thunkAPI.getState(); //   
        const persistedToken = state.auth.token;

        if (persistedToken === null) { //   
            return thunkAPI.rejectWithValue('Unable to fetch user');
        }
        try{
            setToken(persistedToken); //   
            const { data } = await axios.get('/users/current');
            return data; // {name: ..., email: ...} 
        } catch (error){
            return thunkAPI.rejectWithValue(error.response.data.message); 
        }
    }
);